e y encarar de la mejor forma una situación definida, echando mano de los recursos que se encuentren accesibles. Las alternativas de superación realizan alusión al procedimiento, la rehabilitación y la integración social de quienes han desarrollado un trastorno por dependencia a una o algunas sustancias o muestran una forma de consumo de la problemática en ausencia de dependencia. ")]),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("Se encuentran las siguientes alternativas:")]),_vm._m(14),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("A continuación, se describe la tabla de estrategias y líneas de acción del componente de reducción de daños y riesgos donde se tratará el tema de disminución de riesgos y daños: actividades orientadas a disminuir los efectos negativos del uso de drogas, los peligros asociados a las actividades de consumo; y favorecer la accesibilidad a la red asistencial de estos usuarios, incrementando su calidad de vida.")]),_vm._m(15),_vm._m(16),_c('Separador'),_vm._m(17),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("Este eje aspira disponer del mejor soporte viable a nivel nacional y territorial para reducir la incidencia, la prevalencia y el efecto del consumo, por medio de alternativas de contestación técnica, institucional, financiera y de unión. La ubicación, el procedimiento y el apoyo al consumidor, es responsabilidad de todos, por consiguiente, el núcleo familiar, la sociedad y las instituciones tienen que estar preparados para ofrecer una contestación integral según su papel.")]),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("Sin embargo, tomando en cuenta el choque que el consumo tiene sobre la salud pública y la dinámica social se han diseñado y aumentado diferentes tácticas que permiten contestar integralmente al problema del consumo y sus diferentes declaraciones, según su entorno (barrial-comunitario, estudiantil, universitario, gremial y penitenciario).")]),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("Estas son las medidas que para atender la problemática ha venido desarrollando el Ministerio de Salud y Protección Social en el marco de la Política Nacional:")]),_c('div',{staticClass:"row justify-content-center align-items-center mt-5"},[_c('div',{staticClass:"col-12 col-md-10",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"bloque-texto-g color-primario p-3 p-sm-4 p-md-5 mb-5"},[_c('div',{staticClass:"bloque-texto-g__img",style:({'background-image': ("url(" + (require('@/assets/curso/temas/tema2/img15.png')) + ")")})}),_vm._m(18)])])]),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("Estas zonas comprometen a personas correspondientes a la comunidad educativa, a las redes institucionales y a la comunidad local que la rodea. Los actores claves son los que hacen posible movilizar esta modalidad de servicio son: los docentes orientadores en las escuelas y los jefes de bienestar universitario en las instituciones de educación superior.")]),_c('Separador'),_vm._m(19),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("Busca que le garanticen en las políticas públicas, tomando en cuenta los intereses y obligaciones particulares, territoriales y poblacionales, promoviendo el control social ejercido por la ciudadanía y promoviendo la legitimidad con el protagonismo de la sociedad, en una interacción de concertación y creación persistente entre el régimen y la población. Expone condiciones, insumos, procesos y ocupaciones para el diseño, utilización, seguimiento y evaluación de políticas, planes, programas, proyectos, alternativas e intervenciones que viabilicen la atención integral poblacional en peligro o con consumo incierto de sustancias psicoactivas, familias y sociedades.")]),_vm._m(20),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("Para ampliar más información sobre la Ley, puede acceder al siguiente enlace:")]),_vm._m(21)],1)],1)}
